<template>
  <div class="input-wrap shadow">
    <input
      type="text"
      v-model="newItem"
      class="input-box"
      maxlength="30"
      @keyup.enter="addItem"
    />
    <div class="option">
      <span @click="addIcon(0)" class="img1"> Ïù¥ÎØ∏ÏßÄ1 </span>
      <span @click="addItem" class="add-bt">
        <i class="fas fa-plus add-bt-icon"></i>
      </span>
    </div>
    <!-- ÏïàÎÇ¥Ï∞Ω -->
    <ModalView :show="showModal" @closemodal="showModal = false">
      <template #header>
        <h3>ÏïàÎÇ¥Ï∞Ω</h3>
      </template>
      <template #body>
        <h2>ÎÇ¥Ïö©ÏùÑ ÏûëÏÑ±ÌïòÏó¨Ï£ºÏÑ∏Ïöî.</h2>
      </template>
    </ModalView>
    <!--slotÏùÑ ÏÇ¨Ïö©ÌïòÎ†§Î©¥ ÏùºÏ∫êÌï¥ÏïºÎê® -->
  </div>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
import ModalView from "@/components/common/ModalVue.vue";

export default {
  components: {
    ModalView,
  },
  setup() {
    const store = useStore();
    const newItem = ref("");
    const newIcon = ref(0);
    const showModal = ref(false);

    const addItem = () => {
      let temp = newItem.value;
      let icon = newIcon.value;
      temp = temp.trim();
      // Ï∂îÌõÑ ÏóÖÎç∞Ïù¥Ìä∏ ÏòàÏ†ï(Ï†ïÍ∑úÌëúÌòÑÏãù-Î¨∏ÏûêÏó¥Ï≤¥ÌÅ¨ Î¨∏Î≤ï)
      let checktemp = temp.replace(/\s/g, "");
      // ÏïûÏûêÎ¶¨Í≥µÎ∞± Í≥µÎ∞± Îí∑ÏûêÎ¶¨Í≥µÎ∞±
      if (temp !== "" && temp === checktemp) {
        // context.emit('additem', temp, icon); // ÏóêÎ∞ãÏúºÎ°ú Ïñ¥ÎØ∏ÌïúÌÖå Í∞í Ï†ÑÎã¨
        // store.commit('ADD_MEMO', {item:temp, index:icon}); Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÏÜ°ÌïòÎ†§Í≥† Í∞ùÏ≤¥Ïó¥Î°úÌï¥ÏÑú Î≥¥ÎÉÑ
        store.dispatch("fetchAddMemo", { item: temp, index: icon });

        resetItem();
      } else {
        showModal.value = true;
      }
    };
    // ÎÇ¥Ïö© Ïû¨ÏÑ§Ï†ï
    const resetItem = () => {
      newItem.value = "";
    };
    const addIcon = (index) => {
      newIcon.value = index;
    };

    return {
      newItem,
      addItem,
      addIcon,
      showModal,
    };
  },
};
</script>

<style scoped>
.input-wrap {
