  line-height: 50px;
  border-radius: 5px;
  background-color: #fff;
  overflow: hidden;

  margin: 20px 0;
}

.input-wrap input {
  border-style: none;
}

.input-wrap input:focus {
  outline: none;
}

.input-box {
  width: calc(90% - 250px);
  margin-left: 20px;
  font-size: 16px;
  font-weight: 700;
}

.option {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  float: right;
}
/* .img1:active {
  outline: 3px solid red;
} */

.img1 {
  display: block;
